<script>
import publicFn from '../../../untils/Public.js';
import { mapActions, mapGetters, mapState } from 'vuex'
export default {
  //
  created: function() {
    if (!localStorage.getItem('access_token')) {
      this.$router.push({ name: "index" })
    }
    this.$store.commit("pushOderChart", null);
    //id
    var id = this.$route.query.id;
    this.id = id;
    //
    this.postOderChart(id);
    //id
    //
    var caseNo = this.$route.query.caseNo;
    this.caseNo = caseNo;
    //id
  },
  name: 'Expert',
  data: function() {
    return {
      //s
      nex: false,
      //
      recode: null,
      //
      imgList: [
        //              {path:"",id:0},
        //              {imgSrc:"",id:1}
      ],
      // 
      setByAuxiliary: [
        { Auxiliary: "" },
        { Auxiliary: "" },
        { Auxiliary: "" },
        { Auxiliary: "" }
      ],
      //
      workingList: [
        {
          date: "",//
          endTime: "",//
          startTime: "",//
        }
      ],
      recodeShow: false,
      listData: {
        upDataReservation: true,
        list2: {
          bot: false,
          rues: true,
          alerts: true,
          auxiliary: "",//
          ChiefComplaint: "",//  
          MedicalHistoryNow: "",//
          MedicalHistoryOf: "",//
          MedicalHistoryBySelf: "",//
          contagionHistory: "",//
          allergyHistory: "",//
          familyHistory: "",//
          inspect: "",//
          auxiliaryInspect: "",//
          imgList: []//
        }
      },
      //id
      id: null,
      //id
      patientId: null,
      //
      caseNo: null,
      //id
      doctorId: null,
      //id
      expertId: null,
      //
      goPay: false,
      //
      rules: {
        ChiefComplaint: [
          { required: true, message: '', trigger: 'blur' },
          { min: 0, max: 50, message: '50', trigger: 'blur' },
        ],
        MedicalHistoryNow: [
          { required: true, message: '', trigger: 'blur' },
          { min: 0, max: 500, message: '500', trigger: 'blur' },
        ],
        MedicalHistoryOf: [
          { min: 0, max: 200, message: '200', trigger: 'blur' },
        ],
        MedicalHistoryBySelf: [
          { min: 0, max: 200, message: '200', trigger: 'blur' },
        ],
        contagionHistory: [
          { min: 0, max: 200, message: '200', trigger: 'blur' },
        ],
        allergyHistory: [
          { min: 0, max: 50, message: '50', trigger: 'blur' },
        ],
        familyHistory: [
          { min: 0, max: 100, message: '100', trigger: 'blur' },
        ],
        inspect: [
          { min: 0, max: 1000, message: '1000', trigger: 'blur' },
        ],
        auxiliaryInspect: [
          { min: 0, max: 1000, message: '1000', trigger: 'blur' },
        ]
      },
      progressStyle: {
        position: 'absolute',
        left: '50%',
        top: '50%',
        marginLeft: '-50px',
        marginTop: '-50px'
      },//
      percentage: [],//
      progressIsShow: [],//
    }
  },
  methods: {
    ...mapActions({
      recodeListFn: 'recodeListFn',
      postOderChart: "postOderChart"
    }),
    //
    userChaert(id) {
      var url = '/api/patient/patients/' + id;
      var token = localStorage.getItem('access_token');
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + token
        }
      }).then(function(res) {
        //
        this.caseNo = res.body.caseNo;
        //id
        this.patientId = res.body.usersId;
        //             //
        //             var nowDate=publicFn.publicLo.dataChange();
        //             //
        //             var birthdayDate= publicFn.publicLo.dataChange(res.body.birthday);
        //             //
        //             this.age = parseInt(nowDate.y)-parseInt(birthdayDate.y)+1;
      })
    },
    save1: function(a) {
      if (a == 5) {
        //
        if (!this.listData.upDataReservation) return false;
        //
        this.listData.list2.rues = true;
      }
      this.$refs['listData.list2'].validate((valid) => {
        if (valid) {
          //
          if (a == 4) {
            this.listData.list2.rues = false;
            //
            this.postImgs()
          }
        } else {
          document.body.scrollTop = 340;
          return false;
        }
      });
    },
    //
    clere: function() {
      //id
      if (this.recordId) {
        //
        this.listData.list2.rues = false;
      } else {
        //idassistOderIng;
        this.$store.commit('ucliClereFn', 1);
      }
      return
    },

    postImg: function() {
      //that vue this
      var that = this;
      var token = localStorage.getItem('access_token');
      if (this.listData.list2.imgList.length > 19) {
        return
      }
      //
      var imgSrc = publicFn.publicLo.senImg("fileByReservation", function(e) {
        var url = '/api/basic/employees/upload';
        //e.target.result base64
        var image = { imageUrl: e.target.result };
        image = JSON.stringify(image);
        var id = publicFn.publicLo.dataChange();
        id = id.time;
        that.listData.list2.imgList.push({
          "createdBy": '',
          "createdWhen": '',
          "deleteFlag": '',
          "fromId": '',
          "fromType": '',
          "id": '',
          "name": '',
          "path": e.target.result,
          "type": '',
          "updatedBy": '',
          "updatedWhen": '',
          move: id
        });
        that.percentage.push(25)
        that.progressIsShow.push(true)
        //
        that.$http.post(url, image, {
          headers: {
            Authorization: 'bearer ' + token
          },
          emulateJSON: true
        }).then(function(res) {
          // res res.body 
          var res = res.body;
          // idid
          // 
          // 
          that.$set(that.listData.list2.imgList[that.listData.list2.imgList.length - 1], 'path', res.path[0])
          that.$set(that.percentage, that.percentage.length - 1, 100)
          var timer = setTimeout(function() {
            // 
            that.$set(that.percentage, that.percentage.length - 1, 25)
            that.$set(that.progressIsShow, that.progressIsShow.length - 1, false)
          }, 1000)
          //file
          document.getElementById('fileByReservation').value = ''
        }).catch(function(res) {
          // 
          // that.$http.post(url, image, {
          //   headers: {
          //     Authorization: 'bearer ' + token
          //   },
          //   emulateJSON: true
          // }).then(function(res) {
          //   var res = res.body;
          //   var id = publicFn.publicLo.dataChange();
          //   id = id.time;
          //   that.listData.list2.imgList.push({
          //     "createdBy": '',
          //     "createdWhen": '',
          //     "deleteFlag": '',
          //     "fromId": '',
          //     "fromType": '',
          //     "id": '',
          //     "name": '',
          //     "path": res.path[0],
          //     "type": '',
          //     "updatedBy": '',
          //     "updatedWhen": '',
          //     move: id
          //   });
          //   document.getElementById('fileByReservation').value = ''
          // }).catch(function(res) {

          // });
          that.percentage.splice(that.percentage.length - 1, 1)
          that.progressIsShow.splice(that.progressIsShow.length - 1, 1)
        });
      });
    },
    //
    delImg: function(id) {
      //file
      document.getElementById('fileByReservation').value = '';
      //a index
      var a = null;
      for (let i in this.listData.list2.imgList) {
        //
        if (this.listData.list2.imgList[i].move == id) {
          a = i//
        }
      }
      //
      this.listData.list2.imgList.splice(a, 1)
      // this.listData.list2.imgList.splice(a, 1);
      this.percentage.splice(a, 1);
      this.progressIsShow.splice(a, 1)
    },
    //
    postReservation: function(dat) {
      //vuex
      var user = this.$store.state.user;
      //idid
      this.doctorId = user.id;
      //
      var path = this.listData.list2.imgList;
      //param
      var parm = {
        "allergyHistory": this.listData.list2.allergyHistory,// ,
        "auxiliaryCheck": this.listData.list2.auxiliaryInspect,// ,
        "chiefComplaint": this.listData.list2.ChiefComplaint,//
        "familyHistory": this.listData.list2.familyHistory,// 
        "history": this.listData.list2.MedicalHistoryNow,//
        "infectionHistory": this.listData.list2.contagionHistory,// ,
        "pastMedicalHistory": this.listData.list2.MedicalHistoryOf,//
        "personalHistory": this.listData.list2.MedicalHistoryBySelf,// ,
        "recordCheck": this.listData.list2.inspect,//
        "patientId": this.patientId,//id
        "caseNo": this.caseNo,//
        "doctorId": this.doctorId,//id
        expertId: this.expertId,//id
        attachments: path,//
        id: this.recordId,//id0
        orderId: this.id,//id
        age: this.age,//
      };
      var token = localStorage.getItem('access_token');
      var url = '/api/patient/records';
      this.$http.post(url, parm, {
        headers: {
          Authorization: 'bearer ' + token
        }
      }).then(function(res) {
        var res = res.body;
        //id
        this.recordId = res.id;
        this.success("");
        // vuex assisOderIng
        var parms = { recordId: this.recordId, description: parm.chiefComplaint };
        this.$store.commit('ucliFn', parms);
      }).catch(function(res) {
        this.warning("")
      });
    },
    //
    getRescevation: function(res) {
      res = res;
      if (res[0]) {
        //truefalse
        this.recodeShow = true;
        //
        this.recode = res;
        for (let k in res) {
          for (let i in res[k].attachments) {
            //
            this.imgList.push({ path: res[k].attachments[i].path, id: res[k].attachments[i].id })
          }
        }
        this.nex = true;
      }
    },
    //**
    // 
    // **//
    recodFn: function(list) {
      //
      this.listData.list2.imgList = [];
      //
      this.listData.list2.allergyHistory = list.allergyHistory;// ,
      this.listData.list2.auxiliaryInspect = list.auxiliaryCheck;// ,
      this.listData.list2.ChiefComplaint = list.chiefComplaint;//
      this.listData.list2.familyHistory = list.familyHistory;// 
      this.listData.list2.MedicalHistoryNow = list.history;//
      this.listData.list2.contagionHistory = list.infectionHistory;// ,
      this.listData.list2.MedicalHistoryOf = list.pastMedicalHistory;//
      this.listData.list2.MedicalHistoryBySelf = list.personalHistory;// ,
      this.listData.list2.inspect = list.recordCheck;//
      this.recordId = list.id; //id
      //          var time2=publicFn.publicLo.dataChange();
      //          var time3=publicFn.publicLo.timeCheng(list.createdWhen);
      //          time2 = time2.time-86400000;
      //

      this.listData.list2.imgList = list.attachments || [];
      // 
      this.percentage = []
      this.progressIsShow = []
      for (var j = 0; j < this.listData.list2.imgList.length; j++) {
        this.listData.list2.imgList[j]['move']=Math.random()+parseFloat(Math.random())
        this.percentage.push(25)
        this.progressIsShow.push(false)
      }
      //
      var parm = { recordId: this.recordId, description: list.chiefComplaint };
      this.$store.commit('ucliFn', parm);
    },

    postImgs: function() {
      this.postReservation();
    },
    //
    success: function(text) {
      this.$message({
        message: text,
        type: 'success'
      });
    },
    //
    oderChartFn(res) {
      //id
      this.expertId = res.expertId;
      //
      this.recodeListFn(res.usersId);
      //
      this.userChaert(res.patientId);
      this.age = res.age;
    },
    warning: function(text) {
      this.$message({
        message: text,
        type: 'warning'
      });
    },
  },
  mounted: function() {

  },
  computed: {
    //**
    // vuex
    // **//
    recodeListCom: function() {
      var data = this.recodeList;
      //
      this.recode = null;
      if (data) {
        //list
        this.getRescevation(data)
      }
      return 1;
    },
    //**
    // vuex
    // **//
    oderChartState: function() {
      var data = this.oderChart;
      if (data) {
        //
        this.oderChartFn(data)
      }
      return 1;
    },
    ...mapState([
      'recodeList',
      'oderChart'
    ]),
  },
}
</script>



